.rightbox {
  float: left;
  width: 1099px;
  height: 819px;
  overflow: hidden;
}
.rightbox .information {
  width: 384px;
  height: 103px;
  margin-bottom: 29px;
  text-align: center;
}
.rightbox .information a {
  font-family: MicrosoftYaHei;
  font-size: 44px;
  font-weight: normal;
  font-stretch: normal;
  line-height: 103px;
  letter-spacing: 2px;
  color: #ffffff;
  text-decoration: none;
  text-shadow: 0 0 30px #f4faf5;
}
.rightbox .kuang {
  width: 1099px;
  height: 687px;
  overflow: hidden;
}
.rightbox .kuang .beijing {
  width: 1061px;
  height: 635px;
  margin: 35px 0  0 35px;
}
.rightbox .kuang .beijing .tubaio {
  width: 944px;
  height: 308px;
  margin-left: 48px;
  margin-bottom: 37px;
}
.rightbox .kuang .beijing .tubaio .zhexian {
  width: 100%;
  height: 100%;
}
.rightbox .kuang .science {
  width: 100%;
  height: 50%;
}
.rightbox .kuang .science ul {
  float: left;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.rightbox .kuang .science ul li {
  text-align: center;
  float: left;
  width: 33%;
  height: 100%;
}
.rightbox .kuang .science ul li .today {
  margin-bottom: 0px;
  font-size: 26px;
  margin-top: 51px;
  color: #ffffff;
}
.rightbox .kuang .science ul li .num {
  margin-bottom: 0px;
  font-size: 22px;
  font-weight: normal;
  letter-spacing: 0px;
  color: white;
}
.rightbox .kuang .science ul li .num i {
  font-style: normal;
  font-size: 40px;
  font-weight: 700;
  color: #00ffff;
}
.rightbox .kuang .science ul li .wushui {
  width: 298px;
  height: 45px;
  border-radius: 12px;
  border: solid 2px #0090ff;
  margin-left: 26px;
  margin-top: 10px;
}
.rightbox .kuang .science ul li .wushui span {
  font-size: 24px;
  color: #ffffff;
  line-height: 35px;
}
.rightbox .kuang .science ul li .bennian {
  font-size: 24px;
  color: #ffffff;
}
.rightbox .kuang .science ul li .bennian span {
  color: #0090ff;
}
